import Link from 'next/link'
import NavStyles from './styles/NavStyles'
import User from './User'
import Signout from './Signout'
import styled from 'styled-components'
import AutoComplete from './AutoComplete'
import Block from './Block'
import { Component } from 'react'

const Titles = styled.input`
  background: none;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
`

class Nav extends Component {
  constructor(props) {
    super(props)

    this.state = {
      content: '',
    }
  }
  fillContent = e => {
    const { value } = e.target
    this.setState({ content: value })
  }
  render() {
    return (
      <User>
        {({ data: { me } }) => (
          <NavStyles content={this.state.content}>
            {me && (
              <>
                <AutoComplete user={me} />
                <Link href="/inbox">
                  <a className="tab">
                    <Titles
                      readOnly
                      name="input"
                      value="Dashboard"
                      onMouseOver={this.fillContent}
                    />
                  </a>
                </Link>
                <Link href="/feedupload">
                  <a className="tab2">
                    <Titles
                      readOnly
                      name="input2"
                      value="Feed Upload"
                      onMouseOver={this.fillContent}
                    />
                  </a>
                </Link>
                <Link href="/account">
                  <a className="tab3">
                    <Titles
                      readOnly
                      name="input3"
                      value="Admin Settings"
                      onMouseOver={this.fillContent}
                    />
                  </a>
                </Link>
                <Signout />
              </>
            )}
            {!me && (
              <>
                <Link href="/pricing">
                  <a>Pricing</a>
                </Link>
                <Link href="/login">
                  <a>Login</a>
                </Link>
              </>
            )}
          </NavStyles>
        )}
      </User>
    )
  }
}

export default Nav
